/* 第四个页面 */
.icon-con{
    width:40px;
    height:40px;
    fill:#575757;
    vertical-align: middle;
}
.contact-page{
    background: -webkit-gradient(linear,left top, right top,from(#f2f2f2),color-stop(49.9%, #f2f2f2),color-stop(50%, #e9e9e9), to(#e9e9e9));
    background: -webkit-linear-gradient(left,#f2f2f2 0%,#f2f2f2 49.9%,#e9e9e9 50%, #e9e9e9 100%);
    background: -o-linear-gradient(left,#f2f2f2 0%,#f2f2f2 49.9%,#e9e9e9 50%, #e9e9e9 100%);
    background: linear-gradient(to right,#f2f2f2 0%,#f2f2f2 49.9%,#e9e9e9 50%, #e9e9e9 100%);
    -ms-flex: 1 1;
        flex: 1 1;
    height: 100vh;
}
.contact-page > h2{
    text-align: center;
    font-size:40px;
}
.contact-page .contacts {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.contact-page .location{
    padding:20px 0;
    /* display: flex; */
    /* flex-direction: column; */
    /* align-items: center; */
    display: inline-block;
}
.contact-page .location p{
    font-weight: 300;
    font-size:25px;
    -webkit-transition: all .3s ease-in;
    -o-transition: all .3s ease-in;
    transition: all .3s ease-in;
    margin-left:30px;
}
.contact-page .location li{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin:8px 0;
    width: 100%;
}
.contact-page .location li img{
    margin-left:30px;
    width:200px;
    height:200px;
}





