/* 第三个页面 */
.skill-page{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    overflow-y: scroll;
}
.skill-page .skill-info{
    /* max-width:790px; */
    /* margin:0 auto; */
    display: -ms-flexbox;
    display: flex;
}
.skill-page .skill-info ul{
    list-style-type: circle;
    -ms-flex:1 1;
        flex:1 1;
    margin: 0 50px;
    padding: 0 1rem;
}
.skill-page .skill-info ul li{
   margin: 0.8rem 0;
   letter-spacing: 1px;
}
.skill-page .skill-info .highlight{
    font-weight: 400;
    background: #EEEEEE;
    border: 1px solid #cfd8dc;
    border-radius: 3px;
    margin: 0;
    color: #2196f3;
}




